<template>
  <div>
    <div class="center-show" :style="bgImg">
      <div class="icon-show" :class="{'left':isLeft,'right':isRight,'center':isCenter}">
        <img :src="toSon.imgList.logourl">
      </div>

      <div class="main-heading" :style="headingBgImg">
        <h1 v-show="toSon.mainHeadingInputShow" style="margin-top:20px">{{toSon.mainHeading}}</h1>
        <img :src="toSon.imgList.headingurl" v-show="toSon.mainHeadingUploadShow">
      </div>

      <div class="video-show">
        <img :src="toSon.imgList.coverurl" class="cover-show" v-show="coverFade">

        <video controls="controls" :src="toSon.imgList.video"></video>

        <img
          :src="imgList.triangleurl "
          class="triangl-show"
          @click="palyVideo"
          v-show="traiangFade"
        >

        <!-- <video src>视频展示</video> -->
      </div>
      <div
        class="text-show"
        :style="textBgImg"
        :class="{'fontbold':isBold,'fontstyle':isItalic,'textunderline':isUnderLine,'textlinethrough':isLineThrough,'textleft':isTextLeft,'textright':isTextRight,'textcenter':isTextCenter}"
      >{{toSon.textarea}}</div>
      <div class="icon-like" v-show="toSon.textset.isLike">
        <img src="../assets/video/like.png" alt @click="counter += 1">
        <p>{{counter}}</p>
      </div>
    </div>
  </div>
</template>
<script>
import ContentHeader from "../base/content-header";
export default {
  props: ["toSon"],
  name: "video-palet",
  data: function() {
    return {
      bgImg: {
        backgroundImage: "url(" + require("../assets/video/bg.png") + ")",
        backgroundPosition: "center center",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        backgroundColor: ""
      },

      headingBgImg: {
        backgroundImage:
          "url(" + require("../assets/video/heading_bg.png") + ")",
        backgroundPosition: "center center",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover"
      },
      textBgImg: {
        backgroundImage: "url(" + require("../assets/video/textbg.png") + ")",
        backgroundPosition: "center center",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover"
      },

      imgList: {
        headingurl: require("../assets/video/title.png"),
        headingbgimg: require("../assets/video/heading_bg.png"),
        bgurl: require("../assets/video/bg.png"),
        coverurl: require("../assets/video/video.png"),
        triangleurl: require("../assets/video/triangle.png"),
        textbg: require("../assets/video/textbg.png"),
        bgcolor: ""
      },

      mainHeadingInputShow: true,
      mainHeadingUploadShow: false,
      counter: 0,
      coverFade: true,
      traiangFade: true,

      isLeft: true,
      isCenter: false,
      isRight: false,

      isTextLeft: true,
      isTextCenter: false,
      isTextRight: false,

      isBold: "",
      isItalic: "",
      isUnderLine: "",
      isLineThrough: "",

      isLike: false
    };
  },
  watch: {
    toSon: {
      handler(n, o) {
        // console.log(n, "监听toSON");

// 背景
        if (n.imgList.bgurl !== "") {
          // console.log("123");
          let bgurl = n.imgList.bgurl;
          this.backgroundColor = "";
            n.imgList.chooseColor = "";
          this.bgImg.backgroundImage = "url(" + bgurl + ")";
        }

        // 选色器纯色背景
        if (n.imgList.bgcolor !== "") {
          // console.log(456);
          this.bgImg.backgroundImage = "";
          n.imgList.chooseColor = "";
          this.bgImg.backgroundColor = n.imgList.bgcolor;
        }

        // }

        // 纯色背景
        if (n.imgList.chooseColor !== "") {
          this.bgImg.backgroundImage = "";

          this.bgImg.backgroundColor = n.imgList.chooseColor;
        }

        // 改变标题背景图片
        if (n.imgList.headingbg !== "") {
          let headingbgimg = n.imgList.headingbg;
          this.headingBgImg.backgroundImage = "url(" + headingbgimg + ")";
          // console.log(this.headingBgImg.backgroundImage, "背景图片");
        }
        // 改变文字背景图片
        if (n.imgList.textbg !== "") {
          let textBgImg = n.imgList.textbg;
          this.textBgImg.backgroundImage = "url(" + textBgImg + ")";
        }

        if (n.textset !== "") {
          this.isLeft = n.textset.isLeft;
          this.isCenter = n.textset.isCenter;
          this.isRight = n.textset.isRight;

          this.isTextLeft = n.textset.isTextLeft;
          this.isTextCenter = n.textset.isTextCenter;
          this.isTextRight = n.textset.isTextRight;

          this.isBold = n.textset.isTextBold;
          this.isItalic = n.textset.isTextItalic;
          this.isUnderLine = n.textset.isTextUnderLine;
          this.isLineThrough = n.textset.isTextLineThrough;
        }
      },

      deep: true
    }
  },

  methods: {
    palyVideo() {
      this.coverFade = false;
      this.traiangFade = false;
    }
  }
};
</script>
<style scoped>
.left {
  text-align: left;
}
.right {
  text-align: right;
}

.center-show {
  margin: 0 auto;
  width: 376px;
  height: 663px;

  position: relative;
}
.icon-show {
  width: 100%;
  height: 50px;
}
.icon-show img {
  width: 50px;
  height: 35px;
  margin: 15px 10px;
  margin-top: 15px;
}
.main-heading {
  /* vertical-align: middle; */
  margin: 10px 10px 0px 40px;
  height: 100px;
  width: 80%;
  white-space: normal;
  word-break: break-all;
  word-wrap: break-word;
  overflow: hidden;
}
.main-heading img {
  width: 70%;
  height: 70px;
  margin-top: 20px;
}
.heading-bg {
  position: absolute;
  top: 40px;
  left: 40px;
  height: 60px;
  width: 300px;
}

.video-show {
  position: relative;
  margin: 35px 5px -60px 8px;
  width: 95%;
  height: 300px;
}
video {
  width: 100%;
}
.triangl-show {
  width: 50px;
  height: 50px;
  position: absolute;
  margin: 0;
  top: 26%;
  left: 45%;
  z-index: 999;
  /* display: none; */
}

.cover-show {
  position: absolute;
  width: 357px;
  height: 201px;
  z-index: 99;
  /* display: none; */
}
.text-show {
  text-align: left;
  margin: 10px auto;
  width: 90%;
  height: 90px;
  white-space: normal;
  word-break: break-all;
  word-wrap: break-word;
  overflow: hidden;
  /* font-weight: bold; */
  /* text-decoration: line-through; */
  /* text-decoration: underline; */
  /* font-style: italic; */
}
.fontbold {
  font-weight: bold;
}
.fontstyle {
  font-style: italic;
}
.textunderline {
  text-decoration: underline;
}
.textlinethrough {
  text-decoration: line-through;
}

.textright {
  text-align: right;
}
.textleft {
  text-align: left;
}
.textcenter {
  text-align: center;
}

.icon-like {
  position: absolute;
  right: 35px;
  bottom: 15px;
}
@font-face {
  font-family: happy;
  src: url("../assets/text.ttf");
}
h1 {
  font-family: happy;
  font-size: 40px;
  color: #ffffff;
  text-shadow: -3px 3px 0px black;
}
</style>




