<template>
  <div class="mian-container">
    <content-header></content-header>
    <a-row>
      <!-- 模板选择区 -->
      <a-col :span="4" class="aside-left">
        <div class="aside-show">
          <div class="plate-show">
            <video-plate :toSon="setData" v-show="showtemplate"></video-plate>
            <templatesecond v-show="showtemplateSec"></templatesecond>
            <templatethird v-show="showtemplateThi"></templatethird>
          </div>
        </div>

        <div class="aside-footer">
          <a-button @click="clickFade">
            选择模板
            <a-icon type="plus" style="margin-left:20px ;font-size=:16px"></a-icon>
          </a-button>
          <div class="choose-plate" v-show="tempalteIsShow">
            <div class="plate1 plate" @click="chooseTemplate(1)">
              <video-plate :toSon="setData"></video-plate>
            </div>
            <div class="plate2 plate" @click="chooseTemplate(2)">
              <templatesecond></templatesecond>
            </div>
            <div class="plate3 plate" @click="chooseTemplate(3)">
              <templatethird></templatethird>
            </div>
          </div>
        </div>
      </a-col>
      <!-- 视屏展示区域 -->
      <a-col :span="14" class="center-container">
        <p class="center-text">操作提示：可选择需要的模板，点击相应的图片或者文字进行相应的编辑</p>
        <video-plate :toSon="setData" v-show="showtemplate"></video-plate>
        <templatesecond v-show="showtemplateSec"></templatesecond>
        <templatethird v-show="showtemplateThi"></templatethird>
      </a-col>
      <!-- 设置区域 -->
      <a-col :span="6" class="aside-right">
        <div class="setting">
          <a-row>
            <a-col :span="8" class="setting-col col-active">
              基本设置
              <a-icon type="setting" class="setting-icon"></a-icon>
            </a-col>
            <a-col :span="8" class="setting-col">
              高级设置
              <a-icon type="setting" class="setting-icon"></a-icon>
            </a-col>
            <a-col :span="8" class="setting-col">
              咨询帮助
              <a-icon type="setting" class="setting-icon"></a-icon>
            </a-col>
          </a-row>
        </div>
        <!-- 预览...... -->
        <div class="father">
          <div class="mask" v-show="bPreview" @click="setMaskShow"></div>
          <div class="child" id="child" v-show="bPreview">
            <video-plate :toSon="setData"></video-plate>
          </div>
        </div>
        <!-- Logo设置.......... -->
        <div class="content">
          <a-collapse defaultActiveKey="1" :bordered="false">
            <a-collapse-panel>
              <template slot="header">logo</template>

              <div class="content">
                <a-row>
                  <a-col :span="3" offset="1">logo:</a-col>
                  <a-col :span="12" offset="1">
                    <a-upload
                      name="file"
                      :multiple="true"
                      action="http://10.29.15.38:3007/upload"
                      @change="upLoadImg"
                      :showUploadList="false"
                    >
                      <a-button style="width:160px; height:35px" @click="upLoadImgUrl(1)">
                        <a-icon type="upload"/>点击上传图片
                      </a-button>
                    </a-upload>
                  </a-col>
                  <a-col :span="5" offset="2">
                    <a-row>
                      <a-col>
                        <a-icon type="align-left" @click="logoPositon(1)"></a-icon>
                        <a-icon type="align-center" @click="logoPositon(2)"></a-icon>
                        <a-icon type="align-right" @click="logoPositon(3)"></a-icon>
                      </a-col>
                    </a-row>
                  </a-col>
                </a-row>
              </div>
            </a-collapse-panel>
          </a-collapse>
        </div>

        <!-- 标题设置................... -->
        <div class="content">
          <a-collapse defaultActiveKey="1" :bordered="false">
            <a-collapse-panel>
              <template slot="header">标题</template>

              <div class="content">
                <a-row>
                  <a-col :span="6" offset="1">
                    <span>标题：</span>
                  </a-col>
                  <a-col>
                    <a-radio-group :defaultValue="1">
                      <a-radio :value="1" @click="changeHeading(1)">文字输入</a-radio>
                      <a-radio :value="2" @click="changeHeading(0)">图片输入</a-radio>
                    </a-radio-group>
                    <a-input
                      placeholder="输入文字"
                      v-model="setData.mainHeading"
                      v-show="setData.mainHeadingInputShow"
                      style="height:40px"
                    ></a-input>
                    <a-upload
                      action="http://10.29.15.38:3007/upload"
                      v-show="setData.mainHeadingUploadShow"
                      @change="upLoadImg"
                      :showUploadList="false"
                    >
                      <a-input
                        placeholder="点击图标上传视频"
                        style="margin-left:86px ;width:206px;height:40px"
                        @click="upLoadImgUrl(2)"
                      >
                        <a-icon slot="suffix" type="picture"/>
                      </a-input>
                    </a-upload>
                  </a-col>
                  <a-row style="margin-top:10px">
                    <a-col :span="7">辅助图形：</a-col>
                    <a-col :span="15">
                      <a-upload
                        name="file"
                        :multiple="true"
                        action="http://10.29.15.38:3007/upload"
                        @change="upLoadImg"
                        :showUploadList="false"
                      >
                        <a-button style="width:207px" @click="upLoadImgUrl(3)">
                          <a-icon type="upload"/>点击上传图片
                        </a-button>
                      </a-upload>
                    </a-col>
                  </a-row>
                </a-row>
              </div>
            </a-collapse-panel>
          </a-collapse>
        </div>

        <!-- 视频设置................... -->
        <div class="content">
          <a-collapse defaultActiveKey="1" :bordered="false">
            <a-collapse-panel>
              <template slot="header">视频</template>

              <div class="content">
                <a-row>
                  <a-col :span="6" offset="1">
                    <span>
                      <i style="color:red"></i>视频:
                    </span>
                  </a-col>
                  <a-col>
                    <a-upload
                      name="file"
                      :multiple="true"
                      action="http://10.29.15.38:3007/upload"
                      @change="upLoadImg"

                    >
                      <a-button style="width:207px" @click="upLoadImgUrl(7)">
                        <a-icon type="upload"/>点击上传视频
                      </a-button>
                    </a-upload>
                  </a-col>
                  <a-row style="margin-top:20px">
                    <a-col :span="7">辅助图形：</a-col>
                    <a-col :span="15">
                      <a-upload
                        name="file"
                        :multiple="true"
                        action="http://10.29.15.38:3007/upload"
                        @change="upLoadImg"
                        :showUploadList="false"
                      >
                        <a-button style="width:207px" @click="upLoadImgUrl(4)">
                          <a-icon type="upload"/>点击上传图片
                        </a-button>
                      </a-upload>
                    </a-col>
                  </a-row>
                </a-row>
              </div>
            </a-collapse-panel>
          </a-collapse>
        </div>

        <!-- 文本设置...................... -->
        <div class="content">
          <a-collapse defaultActiveKey="1" :bordered="false">
            <a-collapse-panel>
              <template slot="header">文本</template>

              <div class="content">
                <a-row>
                  <a-col :span="5" offset="1">
                    <span>
                      <i style="color:red"></i>文本内容:
                    </span>
                  </a-col>
                  <a-col :span="16" offset="2">
                    <a-icon class="text-icon" type="bold" :data-num="1" @click="changeText(1)"/>
                    <a-icon class="text-icon" type="italic" :data-num="2" @click="changeText(2)"/>
                    <a-icon
                      class="text-icon"
                      type="underline"
                      :data-num="3"
                      @click="changeText(3)"
                    />
                    <a-icon
                      class="text-icon"
                      type="strikethrough"
                      :data-num="4"
                      @click="changeText(4)"
                    />
                    <a-icon
                      class="text-icon"
                      type="align-left"
                      :data-num="5"
                      @click="changeText(5)"
                    />
                    <a-icon
                      class="text-icon"
                      type="align-center"
                      :data-num="6"
                      @click="changeText(6)"
                    />
                    <a-icon
                      class="text-icon"
                      type="align-right"
                      :data-num="7"
                      @click="changeText(7)"
                    />
                  </a-col>
                  <a-textarea
                    placeholder="输入文字"
                    :rows="4"
                    v-model="setData.textarea"
                    maxlength="100"
                    style="width:300px;margin-top:10px "
                  />
                  <a-row style="margin-top:20px">
                    <a-col :span="7">辅助图形：</a-col>
                    <a-col :span="15">
                      <a-upload
                        name="file"
                        :multiple="true"
                        action="http://10.29.15.38:3007/upload"
                        @change="upLoadImg"
                        :showUploadList="false"
                      >
                        <a-button style="width:207px" @click="upLoadImgUrl(5)">
                          <a-icon type="upload"/>点击上传图片
                        </a-button>
                      </a-upload>
                    </a-col>
                  </a-row>
                </a-row>
              </div>
            </a-collapse-panel>
          </a-collapse>
        </div>

        <!-- 其他设置................ -->
        <div class="content">
          <a-collapse defaultActiveKey="1" :bordered="false">
            <a-collapse-panel>
              <template slot="header">其他设置</template>

              <div class="content">
                <p>其他设置</p>

                <a-row>
                  <a-col :span="7" offset="1">
                    <p>点赞设置：</p>
                  </a-col>
                  <a-col :span="16">
                    <a-radio-group defaultValue="select">
                       <a-checkbox value="A" @change="like">显示点赞</a-checkbox>
                    </a-radio-group>
                  </a-col>
                </a-row>
                <a-row style="margin-top:20px;">
                  <a-col :span="4" offset="3">
                    <span>背景：</span>
                  </a-col>
                  <a-col :span="16">
                    <a-upload
                      name="file"
                      :multiple="true"
                      action="http://10.29.15.38:3007/upload"
                      @change="upLoadImg"
                      :showUploadList="false"
                    >
                      <a-button style="width:207px" @click="upLoadImgUrl(6)">
                        <a-icon type="upload"/>点击上传图片
                      </a-button>
                    </a-upload>
                  </a-col>
                </a-row>
                <a-row>
                  <a-col offset="14">
                    <a-checkbox-group>
                      <a-checkbox value="A" @change="bgsetting">使用纯色背景</a-checkbox>
                    </a-checkbox-group>
                  </a-col>
                </a-row>

                <div class="content" v-show="bgShow">
                  <a-row>
                    <a-col :span="6" offset="1">纯色背景:</a-col>
                    <a-col :span="6">
                      <el-color-picker size="medium" @change="changecolor"></el-color-picker>
                    </a-col>
                    <a-col :span="11">
                      <div v-for="(item,index) in chooseColor">
                        <div
                          class="color-item"
                          @click="getcolor"
                          v-bind:style="{backgroundColor:item.color}"
                          :key="index"
                        ></div>
                      </div>
                    </a-col>
                  </a-row>
                </div>
              </div>
            </a-collapse-panel>
          </a-collapse>
        </div>
      </a-col>
    </a-row>
  </div>
</template>
<script>
// console.log($)
import { mapGetters, mapMutations } from "vuex";
import ContentHeader from "../base/content-header";
import VideoPlate from "../base/videopalet.vue";
import templatesecond from "../base/video-template-second.vue";
import templatethird from "../base/video-template-third.vue";
import axios from "axios";

export default {
  name: "APPVideo",

  data: function() {
    return {
      
      chooseColor: [
        { color: "#ffffff" },
        { color: "#ff7171" },
        { color: "#ffaa71" },
        { color: "#ffe071" },
        { color: "#71ff88" },
        { color: "#71ffeb" },
        { color: "#719bff" },
        { color: "#ac92f3" },
        { color: "#9b9b9b" },
        { color: "#000000" }
      ],

      setData: {
        mainHeading: "天翼云",
        textarea: "视频介绍....... ",
        mainHeadingInputShow: true,
        mainHeadingUploadShow: false,

        imgList: {
          logourl: require("../assets/video/logo.png"),
          headingurl: require("../assets/video/title.png"),
          headingbg: "",
          coverurl: require("../assets/video/video.png"),
          bgurl: "",
          bgcolor: "",
          textbg: "",
          value: 0,
          chooseColor: "",
          video:'',
        },
        textset: {
          isLeft: "",
          isCenter: "",
          isRight: "",

          isTextLeft: "",
          isTextCenter: "",
          isTextRight: "",

          isTextBold: false,
          isTextItalic: false,
          isTextUnderLine: false,
          isTextLineThrough: false,
          isLike:false,
        }
      },

      tempalteIsShow: false,
      bgShow: false,
      showtemplate: true,
      showtemplateSec: false,
      showtemplateThi: false
    };
  },

  created() {},

  methods: {
  

    setMaskShow() {
      let bNewPreview = !this.bPreview;
      this.setPreview(bNewPreview);
    },

    // 模板隐藏
    clickFade: function() {
      this.tempalteIsShow = !this.tempalteIsShow;
    },
    // 模板选择
    chooseTemplate: function(value) {
      if (value === 1) {
        this.showtemplate = true;
        this.showtemplateSec = false;
        this.showtemplateThi = false;
      } else if (value === 2) {
        this.showtemplateSec = true;
        this.showtemplate = false;
        this.showtemplateThi = false;
      } else {
        this.showtemplateThi = true;
        this.showtemplate = false;
        this.showtemplateSec = false;
      }
    },

    // 改变LOGO位置

    logoPositon: function(value) {
      if (value === 1) {
        this.setData.textset.isLeft = true;
        this.setData.textset.isCenter = false;
        this.setData.textset.isRight = false;
        // console.log("1");
      } else if (value === 2) {
        this.setData.textset.isLeft = false;
        this.setData.textset.isCenter = true;
        this.setData.textset.isRight = false;
        // console.log("2");
      } else {
        this.setData.textset.isLeft = false;
        this.setData.textset.isCenter = false;
        this.setData.textset.isRight = true;
        // console.log("3");
      }
    },

    // 标题文字和图片上传切换
    changeHeading: function(value) {
      if (value === 1) {
        this.setData.mainHeadingInputShow = true;
        this.setData.mainHeadingUploadShow = false;
      } else {
        this.setData.mainHeadingInputShow = false;
        this.setData.mainHeadingUploadShow = true;
      }
    },

    // 上传图片
    upLoadImgUrl: function(value) {
      this.setData.imgList.value = value;
     
    },
    upLoadImg: function(info) {
      //  LOGO
      if (this.setData.imgList.value === 1) {
      
       this.setData.imgList.logourl = "http://10.29.15.38:3007/getPic?path="+info.fileList[ info.fileList.length-1].response
       console.log(info,'图片')
        // 图片标题
      } else if (this.setData.imgList.value === 2) {
        this.setData.imgList.headingurl = "http://10.29.15.38:3007/getPic?path="+info.fileList[ info.fileList.length-1].response
        // console.log('图片')
        // 标题背景
      } else if (this.setData.imgList.value === 3) {
        this.setData.imgList.headingbg = "http://10.29.15.38:3007/getPic?path="+info.fileList[ info.fileList.length-1].response
        // console.log(this.setData.imgList.headingbg)
        // console.log(info ,'图片标题')
        // console.log(info.file.name)
        // 视频封面
      } else if (this.setData.imgList.value === 4) {
        this.setData.imgList.coverurl =  "http://10.29.15.38:3007/getPic?path="+info.fileList[ info.fileList.length-1].response
        //  console.log(info,'视频封面')
        // 文字背景
      } else if (this.setData.imgList.value === 5) {
        this.setData.imgList.textbg = "http://10.29.15.38:3007/getPic?path="+info.fileList[ info.fileList.length-1].response
      }

      // 背景图片
      else if (this.setData.imgList.value === 6){
        this.setData.imgList.bgurl =  "http://10.29.15.38:3007/getPic?path="+info.fileList[ info.fileList.length-1].response
        this.setData.imgList.bgcolor = "";
        this.setData.imgList.chooseColor = "";
      } 
      //上传视频
      else {
        this.setData.imgList.video= "http://10.29.15.38:3007/getPic?path="+info.fileList[ info.fileList.length-1].response
          console.log(info,'视频')
      }
       
      
    },
    // 改变文字样式
    changeText: function(value) {
      if (value === 1) {
        this.setData.textset.isTextBold = !this.setData.textset.isTextBold;
      } else if (value === 2) {
        this.setData.textset.isTextItalic = !this.setData.textset.isTextItalic;
      } else if (value === 3) {
        this.setData.textset.isTextUnderLine = !this.setData.textset
          .isTextUnderLine;
      } else if (value === 4) {
        this.setData.textset.isTextLineThrough = !this.setData.textset
          .isTextLineThrough;
      } else if (value === 5) {
        this.setData.textset.isTextLeft = true;
        this.setData.textset.isTextCenter = false;
        this.setData.textset.isTextRight = false;
      } else if (value === 6) {
        this.setData.textset.isTextLeft = false;
        this.setData.textset.isTextCenter = true;
        this.setData.textset.isTextRight = false;
      } else {
        this.setData.textset.isTextLeft = false;
        this.setData.textset.isTextCenter = false;
        this.setData.textset.isTextRight = true;
      }
    },
    // 纯色背景
    bgsetting: function() {
      this.bgShow = !this.bgShow;
      this.setData.imgList.bgurl = "";
      this.setData.imgList.chooseColor = "";
    },

    // 选色器
    changecolor: function(event) {
      this.setData.imgList.bgcolor = event;
    },
    getcolor: function(e) {
      this.setData.imgList.chooseColor = e.target.style.backgroundColor;
      this.setData.imgList.bgurl = "";
      this.setData.imgList.bgcolor = "";
    },
    // 点赞
    like:function(){
       this.setData.textset.isLike=! this.setData.textset.isLike
      // console.log(this.isLike)

    },

    ...mapMutations({ setuserData: "setuserData" }),
    ...mapMutations({ setPreview: "setPreview" })
  },
  computed: {
    ...mapGetters(["bPreview"]),
    ...mapGetters(["userData"])
  },


  updated() {
    let newData = this.setData;
    this.setuserData(newData);
    // console.log(this.userData);
  },
  components: { ContentHeader, VideoPlate, templatesecond, templatethird }
};
</script>
<style scoped>
/* mian */
.mian-container {
  margin: 0 auto;
  width: 1440px;
  height: 800px;
  box-shadow: 1px 1px 10px gray;
}
.aside-left {
  box-shadow: 1px 1px 5px gray;
  height: 760px;
}
.aside-show {
  width: 100%;

  height: 90%;
}
.plate-show {
  width: 100px;
  height: 200px;

  margin: 0 auto;
  position: relative;
}
.plate-show div {
  position: absolute;
  left: -80px;
  top: -65px;

  width: 200px;
  height: 200px;
  -webkit-transform: scale(0.25);
}

.choose-plate {
  width: 100%;
  height: 400px;
  border-top: 1px solid gray;
  margin-top: -400px;
}
.plate {
  float: left;
  width: 100px;
  height: 180px;
  border: 1px solid;
  margin-top: 10px;
  margin-left: 10px;
}
.aside-footer {
  text-align: center;
  bottom: 0px;
  height: 10%;
  width: 100%;
}
.aside-footer button {
  font-size: 18px;
  font-weight: bold;
  position: absolute;
  left: 50%;
  margin-left: -65px;
  bottom: 0px;
}
.plate1.plate {
  position: relative;
}
.plate1.plate div {
  -webkit-transform: scale(0.25);
  position: absolute;
  top: -240px;
  left: -138px;
}
.plate2.plate {
  position: relative;
}
.plate2.plate div {
  -webkit-transform: scale(0.25);
  position: absolute;
  top: -240px;
  left: -138px;
}
.plate3.plate {
  position: relative;
}
.plate3.plate div {
  -webkit-transform: scale(0.25);
  position: absolute;
  top: -240px;
  left: -138px;
}

/* center */
.center-text {
  padding-top: 10px;
  font-size: 10px;
  color: gray;
}
/* right */
.aside-right {
  height: 760px;
  position: relative;

  overflow-y: scroll;
}
.setting {
  width: 100%;
  height: 46px;
  font-size: 16px;

  border-bottom: 1px solid rgba(128, 128, 128, 0.445);
}
.setting-col {
  padding-top: 10px;
  height: 46px;
}
.col-active {
  color: #1890ff;
  border-bottom: 1px solid #1890ff;
}
.setting-icon {
  margin-left: 5px;
}
.content {
  text-align: left;
  margin: 0 auto;
  height: auto;
  margin-top: 10px;
  width: 100%;
}
.logo {
  height: 100px;
  text-align: left;
}
.text-icon {
  margin-left: 10px;
  width: 10px;
}

.color-choose {
  width: 140px;
  height: 36px;
}
.color-item {
  width: 15px;
  height: 15px;

  margin: 1px 6px;
  float: left;
}
.father {
  width: 100%;
}
.mask {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: #000;
  opacity: 0.3;
  z-index: 998;
}
button {
  width: 100px;
  height: 40px;
  line-height: 40px;
  text-align: center;
}
.child {
  position: fixed;
  width: 400px;
  height: 400px;
  /* border: 1px solid #ccc; */
  text-align: center;
  /* line-height: 400px; */
  top: calc(50% - 437px);
  left: calc(50% - 259px);
  /* background: #fff; */
  /* top:90px;
    left: 442px; */
  z-index: 999;
}
</style>








