<template>
<div class="votepage">
  <p class="votepage-head">
    <a-icon type="left" class="head-left"></a-icon>
    <span>翼企投</span>
    <a-icon type="appstore" class="head-right"></a-icon>
  </p>
  <div class="votepage-contentp">
    <p class="votepage-title">
      <span>投票标题:</span>
      <span class="votepage-editcontent">{{sVoteTitle}}</span>
      <img class="title-right" src="../../assets/vote/share.jpg">
    </p>
    <p class="votepage-type" v-if="sVoteType == 0">(单选)</p>
    <p class="votepage-type" v-if="sVoteType == 1">(多选)</p>
    <p>{{sVoteDescript}}</p>
  </div>
  <!--文字选项-->
  <div v-show="oVoteChoice.nOption == 0">
    <p class="votepage-contentp" v-for="(item, index) in oVoteChoice.aVoteTextChoice" :key="index">
      <span>选项:</span>
      <span class="votepage-editcontent">{{item.value}}</span>
    </p>
  </div>
  <!--图片选项-->
  <div v-show="oVoteChoice.nOption == 1">
    <p class="votepage-contentp votepage-img" v-for="(item, index) in oVoteChoice.aVoteImgChoice" :key="index">
      <img v-show="item.imgurl !==''" class="choice-img" :src="item.imgurl" alt="photo">
      <img v-show="item.imgurl ==''" class="choice-img" src="../../assets/vote/default.png" alt="photo">
      <span>{{item.descript}}</span>
    </p>
  </div>
  <p class="votepage-time">
    <span>投票截止日期:</span>
    <span>{{sVoteTime}}</span>
  </p>
  <p class="votepage-tovote">
    <a-button class="tovote-bt" type="primary" block>投票</a-button>
  </p>
  <!--匿名投票-->
  <!-- <p>{{sVoteWay}}</p> -->
</div>
</template>

<script>
import {mapGetters} from 'vuex'
export default{
  name: 'vote-basesetting-votetype',
  computed: {
    ...mapGetters(['sVoteType', 'sVoteTime', 'sVoteWay', 'sVoteTitle', 'sVoteDescript', 'oVoteChoice'])
  }
}
</script>
<style scoped>
.votepage{
  width: 383px; height: 675px;
  overflow: scroll; text-align: left;
  background-color: #f0f2f5;
}
.votepage::-webkit-scrollbar{ display: none;}
.votepage-head{
  height: 44px; padding: 9px; margin-bottom: 8px;
  text-align: center; font-size: 25px; line-height: 25px; font-weight: bold;
  background-color: #fff;
}
.votepage-head .anticon{ font-size: 20px;}
.head-left{float: left; margin-top: 2px; color: #108EE9;}
.head-right{float: right; margin-top: 2px; color: #108EE9;}

.votepage-contentp{
  padding: 20px 15px; margin-bottom: 8px;
  background-color: #fff;
  font-size: 16px; color: #000;
}
.votepage-contentp p{ margin: 0;}
.votepage-title{
  font-size: 20px; color: rgba(0,0,0,0.85); font-weight: bold;
}
p.votepage-type{
  margin: 8px 0 12px 0;
  font-size: 16px; color:rgb(0, 140, 255);
}
.title-right{
  float: right;
  width: 20px; margin-top: 3px;
}
p.votepage-img{ padding: 8px 15px;}
.choice-img{ width: 79px; height: 79px;}

.votepage-time{
  padding-left: 15px;
  font-size: 14px; color: #888;
}
.votepage-tovote{
  font-size: 18px;
  padding: 0 15px; margin-top: 32px;
}
.tovote-bt{
  height: 47px;
}
</style>



