<template>
  <div class="appstore">
    <!--轮播-->
    <swiper :options="swiperOption" ref="mySwiper">
      <swiper-slide v-for="(slide,index) in aSwiperSlides" :key="index + 1">
        <img class="swiperimg" :src=slide.url>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
    <!--应用中心-->
    <div>
      <p class="appstore-appsearch">
        <span class="appsearch-title">应用中心</span>
        <span class="toright">
          <a-select class="appsearch-type" placeholder="选择分类" style="width: 224px">
            <a-icon slot="suffixIcon" type="bars"></a-icon>
            <a-select-option value="应用分类">分类1</a-select-option>
          </a-select>
          <a-input-search placeholder="请输入名称" style="width: 224px">
          </a-input-search>
        </span>
      </p>
      <div class="applist">
        <single-app v-for="(item,index) in aAppdata" :key="index + 1" :sApp="item"></single-app>
      </div>
    </div>
    <!--分页-->
    <div class="appstore-pagination">
      <a-locale-provider :locale="zh_CN">
      <a-pagination showSizeChanger showQuickJumper
                    @showSizeChange="fSizeChange"
                    @change="fCurrentChange"
                    v-model="currentPage"
                    :pageSizeOptions="pageSizes"
                    :pageSize="pageSize"
                    :total="totalCount">
      </a-pagination>
      </a-locale-provider>
    </div>
  </div>
</template>

<script>
import register from '../assets/app/app.png'
import questionnaire from '../assets/app/questionnaire.png'
import vote from '../assets/app/vote.png'
import product from '../assets/app/product.png'
import micromv from '../assets/app/micromv.png'
import scanpic from '../assets/app/scanpic.png'
import email from '../assets/app/email.png'
import questionanswer from '../assets/app/questionanswer.png'
import slideimg2 from '../assets/app/swipeimg2.png'
import slideimg3 from '../assets/app/swipeimg3.png'
import slideimg4 from '../assets/app/swipeimg4.png'
import slideimg5 from '../assets/app/swipeimg5.png'

import zh_CN from 'ant-design-vue/lib/locale-provider/zh_CN'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import SingleApp from '../base/centerapp/single-app'
export default {
  name: 'AppStore',
  data () {
    return {
      zh_CN,
      // 轮播图相关数据
      swiperOption: {
        autoplay: {
          autoplay: 3000,
          autoplayStopOnLast: true
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      },
      aSwiperSlides: [
        {url: slideimg2},
        {url: slideimg3},
        {url: slideimg4},
        {url: slideimg5}
      ],
      // app列表模拟数据
      aAppdata: [
        {
          url: vote,
          name: '投票活动',
          descript: '活动简介说明说明说明',
          state: '已添加'
        }, {
          url: micromv,
          name: '微视频',
          descript: '活动简介说明说明说明',
          state: '已添加'
        }, {
          url: email,
          name: '邮件信',
          descript: '活动简介说明说明说明',
          state: '已添加'
        }, {
          url: scanpic,
          name: '扫码页',
          descript: '活动简介说明说明说明',
          state: '已添加'
        }, {
          url: questionnaire,
          name: '问卷调查',
          descript: '活动简介说明说明说明',
          state: '添加'
        }, {
          url: product,
          name: '产品手册',
          descript: '活动简介说明说明说明',
          state: '添加'
        }, {
          url: register,
          name: '做任务',
          descript: '活动简介说明说明说明',
          state: '添加'
        },
        {
          url: questionanswer,
          name: '问答',
          descript: '活动简介说明说明说明',
          state: '添加'
        }

      ],
      // 分页
      // 总数据
      tableData: [],
      // 默认显示第几页
      currentPage: 1,
      // 总条数，根据接口获取数据长度(注意：这里不能为空)
      totalCount: 8,
      // 个数选择器（可修改）
      pageSizes: ['8', '12', '16'],
      // 默认每页显示的条数（可修改）
      pageSize: 8
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    }
  },
  created () {
    this.getData(this.currentPage, this.pageSize)
  },
  methods: {
    // 操作swiper
    // this.swiper.slideTo(3, 1000, false)
    // 分页
    // 将页码，及每页显示的条数以参数传递提交给后台
    getData (n1, n2) {
      var nDataStart = parseInt((n1 - 1) * n2)
      var nDataEnd = parseInt((n1 * n2) - 1)
      console.log(nDataStart, nDataEnd)
      // 使用axios获取数据
      console.log('页数' + n1)
      console.log('条数' + n2)
    },
    fSizeChange (current, size) {
      this.pageSize = size
      this.getData(current, size)
    },
    fCurrentChange (page, pageSize) {
      this.getData(page, pageSize)
    }
  },
  components: {SingleApp, swiper, swiperSlide}
}
</script>

<style scoped>
.appstore{
  width: 1160px; box-sizing: border-box; padding: 0 60px; margin: auto;
  text-align: left;
}
/*轮播样式*/
.swiper-container{
  height: 330px; position: relative;
}
.swiperimg{
  display: block; margin: auto;
  width: 1040px; height: 299px;
  border-radius: 4px;
}
.swiper-pagination{
  position: absolute; bottom: 0;
}
/*应用中心*/
.appstore-appsearch{
  margin: 0; padding: 26px 0 9px 19px;
}
.appstore-appsearch::after{
  content:""; display: block;
  height: 0; clear: both;
}
.appsearch-title{opacity: 0.85; font-size: 16px; font-weight: bold;}
.toright{ float: right;}
.appsearch-type{ margin-right: 18px;}
.applist{
  display: flex; flex-wrap: wrap;
}
/* 分页 */
.appstore-pagination{
  margin-top: 41px;
}
.ant-pagination{
  float: right;
}
</style>

<style>
/*分页样式覆盖*/
.ant-pagination-options{
  margin-left: 8px;
}
/*轮播图原点样式覆盖,(全局)*/
.swiper-pagination-bullet{
  width: 12px;
  height: 12px;
  display: inline-block;
  border-radius: 6px;
  background: #000;
  opacity: 0.2;
  transition: 0.4s;
}
.swiper-pagination-bullet-active{
  opacity: 1;
  background: #3b7dff;
  width: 32px;
}
</style>



