import Vue from 'vue'
import Router from 'vue-router'
// 应用中心
import CenterApp from '@/components/CenterApp'
import AppStore from '@/components/AppStore'
import AppDetail from '@/base/centerapp/AppDetail'
// 投票应用
import GenerateVote from '@/components/GenerateVote'
import VoteBasesetting from '@/base/generatevote/vote-basesetting'
import VoteConsulthelp from '@/base/generatevote/vote-consulthelp'

import APPVideo from '@/components/APPVideo'
import videopalet from '@/base/videopalet'
import templatethird from '@/base/video-template-third'
import templatesecond from '@/base/video-template-second'
import scancode from '@/components/AppScanCode'
import scancodetemplate from '@/base/scancode-template'
import mailletter from '@/components/AppMailLetter'

import qarelease from '@qa/views/qa-release'

Vue.use(Router)

export default new Router({
  routes: [
    // 重定向，没有默认路径打包出错
    {
      path: '/',
      redirect: '/centerapp'
    },
    /* 中台应用 */
    {
      path: '/centerapp',
      name: 'CenterApp',
      component: CenterApp
    },
    {
      path: '/appstore',
      name: 'AppStore',
      component: AppStore
    },
    {
      path: '/appdetail',
      name: 'AppDetail',
      component: AppDetail
    },
    /* 发起投票 */
    {
      path: '/generatevote',
      name: 'GenerateVote',
      component: GenerateVote,
      redirect: '/generatevote/basesetting',
      children: [
        {
          path: 'basesetting',
          name: 'VoteBasesetting',
          component: VoteBasesetting
        },
        {
          path: 'consulthelp',
          name: 'VoteConsulthelp',
          component: VoteConsulthelp
        }
      ]
    },
    {
      path: '/appvideo',
      name: 'APPVideo',
      component: APPVideo,
      children: [
        {
          path: 'videopalet',
          name: 'videopalet',
          component: videopalet
        },
        {
          path: 'video-template-third',
          name: 'templatethird',
          component: templatethird
        },
        {
          path: ' video-template-second',
          name: 'templatesecond',
          component: templatesecond
        }
      ]
    },
    {
      path: '/appscancode',
      name: 'scancode',
      component: scancode,
      children: [
        {
          path: 'scancodetemplate',
          name: 'scancodetemplate',
          component: scancodetemplate
        }
      ]
    },
    {
      path: '/appmailletter',
      name: 'mailletter',
      component: mailletter

    },
    {
      path: '/qarelease',
      name: 'qarelease',
      component: qarelease
    }
  ]
})



