<template>
  <div class="vote-basesetting-complete">
    <div class="votepage-all">
      <p class="votepage-head">
        <a-icon type="left" class="head-left"></a-icon>
        <span>翼企投</span>
      </p>
      <p class="votepage-contentp">
        {{sVoteTitle}}
      </p>
      <p class="votepage-myvote">
        <a-icon type="appstore" class="head-right"></a-icon>
        <span>我的投票</span>
      </p>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  name: 'vote-basesetting-complete',
  computed: {
    ...mapGetters(['sVoteTitle'])
  }
}
</script>

<style scoped>
.vote-basesetting-complete{
  width: 383px; height: 675px;
  overflow: scroll; text-align: left;
  background-color: #f0f2f5;
}
.votepage-all{
  height: 100%;
  position: relative;
}
.vote-basesetting-complete::-webkit-scrollbar{ display: none;}
.votepage-head{
  height: 44px; padding: 9px; margin-bottom: 8px;
  text-align: center; font-size: 25px; line-height: 25px; font-weight: bold;
  background-color: #fff;
}
.votepage-head .anticon{ font-size: 20px;}
.head-left{float: left; margin-top: 2px; color: #108EE9;}

.votepage-contentp{
  padding: 20px 15px; margin-bottom: 8px;
  background-color: #fff;
  font-size: 16px; color: #000;
}

.votepage-myvote{
  position:absolute; bottom: 0;
  width: 100%; height: 50px; padding: 15px; margin: 0;
  text-align:center; background-color: #fff; color: #108EE9;
}
</style>



