<template>
<div class="vote-basesetting-pushsetting">
  <p class="push-head">
    <a-icon type="left" class="head-left"></a-icon>
    <span class="push-head-center">封面标题</span>
    <a-icon type="appstore" class="head-right"></a-icon>
  </p>
  <div class="push-content">
    <div class="push-content-content">
      <p class="push-content-head">{{sVotePushname}}</p>
      <p class="push-content-detail">{{sVotePushcontent}}</p>
    </div>
    <div class="push-content-img">
      <img v-if="sVotePushImgurl == ''" src="../../assets/vote/pushpic.jpg">
      <img v-show="sVotePushImgurl !== ''" :src="sVotePushImgurl">
    </div>
  </div>
  <p class="push-tips">请设置标题、描述和封面</p>
</div>
</template>

<script>
import {mapGetters} from 'vuex'
export default{
  name: 'vote-basesetting-pushsetting',
  computed: {
    ...mapGetters(['sVotePushname', 'sVotePushImgurl', 'sVotePushcontent'])
  }
}
</script>

<style scoped>
.vote-basesetting-pushsetting{
  width: 383px; height: 675px;
  overflow: scroll; text-align: left;
  background-color: #f0f2f5;
}
.vote-basesetting-pushsetting::-webkit-scrollbar{ display: none;}

.push-head{
  height: 44px; padding: 9px 14px; margin-bottom: 8px;
  text-align: center; font-size: 25px; line-height: 25px; font-weight: bold;
  background-color: #fff;
}
.push-head .anticon{ font-size: 20px;}
.push-head-center{  font-size: 25px ;color: rgba(0,0,0,0.85); font-weight: bold;}
.head-left{float: left; margin-top: 2px; color: #108EE9;}
.head-right{float: right; margin-top: 2px; color: #108EE9;}

.push-content{
  padding: 8px 15px; margin: auto;
  height: 95px;
  background-color: #fff;
  position: relative;
}
.push-content-head{ font-size: 16px; font-weight: bold; margin-bottom: 2px;}
.push-content-detail{
  width: 249px;
  font-size: 15px; color: #888;
}
.push-content-img{
  position: absolute; right: 15px; top: 8px;
  width: 79px; height: 79px;
}
.push-content-img img{ width: 79px; height: 79px;}
.push-tips{ padding: 12px 0 0 14px; font-size: 14px; color: #888;}
</style>



