import drawerleft from '../components/drawer-left'
import drawerright from '../components/drawer-right'
import {mapState, mapMutations} from 'vuex'
export default {
  name: 'qarelease',
  components: {drawerleft, drawerright},
  computed: mapState({ //映射state

  }),
  methods:{
  },
  mounted: function() {
    
  }
}
</script>


<style lang="scss" scoped>
.qa-container {
  width: 100vw;
  height: 100vh;
}

.qa-content {
  // position: fixed;
  // top: 0;
  // left: 0;
  width: 100%;
  height: 100%;
  padding: 0 400px 0 200px;
  background: #f5f6fa;
}

.breadcrumb {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  position: relative;
  z-index: 1010;
  width: 100%;
  height: 64px;
  background: white;
  box-shadow: -2px 0 8px rgba(0, 0, 0, 0.15);
}
</style>



