<template>
  <div class="select-content">
    <!--步骤条-->
    <div class="select-content-steps">
      <a-steps class="content-steps" labelPlacement="vertical" :current="nCurrentPage">
        <a-step class="content-step" title="投票类型"></a-step>
        <a-step class="content-step" title="投票设置"></a-step>
        <a-step class="content-step" title="分享设置"></a-step>
        <!--a-step class="content-step" title="完成设置"></a-step-->
      </a-steps>
    </div>
    <!--内容-->
    <div class="steps-content">
      <!--投票类型-->
      <div v-show="nCurrentPage == 0" class="vote-type">
        <a-row>
          <a-col class="title-must ant-row-title" :span="4">投票类型:</a-col>
          <a-col :span="17" :offset="1">
            <a-select class="ant-selects" showSearch placeholder="请选择投票类型" @change="fSelectOnChange">
              <a-select-option value= '单选'>单选投票</a-select-option>
              <a-select-option value= '多选'>多选投票</a-select-option>
            </a-select>
          </a-col>
        </a-row>
        <a-row>
          <a-col class="title-must ant-row-title" :span="4">截止日期:</a-col>
          <a-col :span="17" :offset="1">
            <a-date-picker format="YYYY-MM-DD HH:mm:ss" :locale="zh_CN"
                          :showTime="{ defaultValue: moment('00:00:00', 'HH:mm:ss') }"
                           @change="fDateOnChange" placeholder="活动截止时间"></a-date-picker>
          </a-col>
        </a-row>
        <a-row class="vote-type-vote">
          <a-col class="title-must ant-row-title" :span="4">匿名投票:</a-col>
          <a-col :span="17" :offset="1">
            <a-radio-group
              :options="aWayRadio"
              @change="fRadioOnChange"
              :defaultValue="aWayRadio[0]">
            </a-radio-group>
          </a-col>
        </a-row>
        <a-row class="vote-type-vote">
          <a-col class="title-must ant-row-title" :span="4">限制传播:</a-col>
          <a-col :span="17" :offset="1">
            <a-radio-group
              :options="aVoteLimit"
              @change="fLimitOnChange"
              :defaultValue="aVoteLimit[0]">
            </a-radio-group>
          </a-col>
        </a-row>
      </div>
      <!--投票设置-->
      <div v-show="nCurrentPage == 1" class="vote-setting">
        <a-row class="vote-setting-base">
          <a-row>
            <a-col class="title-must ant-row-title" :span="4">投票标题:</a-col>
            <a-col :span="17" :offset="1">
              <a-input v-model="sVoteTitle" placeholder="请输入投票标题"></a-input>
            </a-col>
          </a-row>
          <a-row>
            <a-col class="ant-row-title" :span="4">详细说明:</a-col>
            <a-col :span="17" :offset="1">
              <a-textarea v-model="sVoteDescript" placeholder="请输入详细说明" rows="3"></a-textarea>
            </a-col>
          </a-row>
        </a-row>
        <!--选项-->
        <a-row class="vote-setting-choice">
          <a-row>
            <a-col :span="4" class="ant-row-title ant-row-title-center">选项设置</a-col>
            <a-col :span="17" :offset="1" class="vote-setting-choiceradio">
              <a-radio-group
                :options="aChoiceRadio"
                @change="fChoiceRadioOnChange"
                :defaultValue="aChoiceRadio[0]">
              </a-radio-group>
            </a-col>
          </a-row>
          <!--文字选项-->
          <div v-show="oVoteChoice.nOption == 0">
            <a-row
              class="vote-setting-choicewhich"
              v-for="(item,key) in oVoteChoice.aVoteTextChoice"
              :key="key">
              <a-col class="ant-row-title" :span="4">投票选项:</a-col>
              <a-col :span="16" :offset="1">
                <a-input v-model="item.value" placeholder="请输入投票选项"></a-input>
              </a-col>
              <a-col class="pop-choice" v-if="key > 1"
                  @click="fChoiceReduceClick(key)"
                  @mouseover="fChoiceMouseover($event)"
                  @mouseout="fChoiceMouseout($event)"
                  :span="1" :offset="1">
                <a-icon type="close-circle" theme="filled" style="font-size:15px"></a-icon>
              </a-col>
            </a-row>
          </div>
          <!--图片选项-->
          <div v-show="oVoteChoice.nOption == 1">
            <div class="vote-setting-choicewhich" v-for="(item, key) in oVoteChoice.aVoteImgChoice" :key="key">
              <a-row>
                <a-col class="ant-row-title" :span="4">投票选项:</a-col>
                <a-col :span="16" :offset="1">
                  <a-upload name="file"
                          action="return"
                          :beforeUpload="fPushImg">
                    <a-button @click = "fCurrentImgChoice(key)">
                      <a-icon type="upload"></a-icon>点击上传图片
                    </a-button>
                  </a-upload>
                </a-col>
                <a-col class="pop-choice" v-if="key > 1"
                      @click="fChoiceReduceClick(key)"
                      @mouseover="fChoiceMouseover($event)"
                      @mouseout="fChoiceMouseout($event)"
                     :span="1" :offset="1">
                  <a-icon type="close-circle" theme="filled" style="font-size:15px;"></a-icon>
                </a-col>
              </a-row>
              <a-row>
                <a-col class="ant-row-title" :span="4">说明内容:</a-col>
                <a-col :span="16" :offset="1">
                  <a-input v-model="item.descript" placeholder="请输入说明内容"></a-input>
                </a-col>
              </a-row>
            </div>
          </div>
          <!--新增按钮-->
          <a-row>
            <a-col :span="24">
              <a-button @click="fChoiceAddClick" class="ant-row-add">
                <a-icon type="plus"></a-icon>
                <span>新增按钮</span>
              </a-button>
            </a-col>
          </a-row>
        </a-row>
      </div>
      <!--分享设置-->
      <div v-show="nCurrentPage == 2">
        <a-row class="vote-sharingsetting">
          <a-row>
            <a-col class="title-must ant-row-title" :span="4">推送名称:</a-col>
            <a-col :span="16" :offset="1">
              <a-input v-model="sPushName" placeholder="请输入投票标题"></a-input>
            </a-col>
          </a-row>
          <a-row>
            <a-col class="ant-row-title" :span="4">推送封面:</a-col>
            <a-col :span="16" :offset="1">
                <a-upload name="file"
                          action="return"
                          :beforeUpload="fPushImg">
                  <a-button @click="fCurrentImgChoice(1000)">
                    <a-icon type="upload"></a-icon>点击上传图片
                  </a-button>
                </a-upload>
              </a-col>
          </a-row>
          <a-row>
            <a-col class="ant-row-title" :span="4">推送说明:</a-col>
            <a-col :span="17" :offset="1">
              <a-textarea v-model="sPushContent" placeholder="请输入详细描述" rows="3"></a-textarea>
            </a-col>
          </a-row>
        </a-row>
      </div>
      <!--完成设置-->
      <div v-show="nCurrentPage == 3" class="vote-completesetting">
        <img class="complete-img" src="../../assets/vote/complete.png">
        <p class="complete-p">设置成功</p>
      </div>
    </div>
    <!--按钮-->
    <p v-show="nCurrentPage < 3" class="content-bt">
      <a-button
        @click="fPrestepClick"
        v-show="nCurrentPage > 0 && nCurrentPage < 3"
        class="ant-bt bt-topre">上一步</a-button>
      <a-button @click="fNextstepClick" v-show=" nCurrentPage < 2" class="ant-bt">下一步</a-button>
      <a-button @click="fSavestepClick" v-show=" nCurrentPage == 2" class="ant-bt">完成</a-button>
    </p>
  </div>
</template>

<script>
import moment from 'moment'
import { mapGetters, mapMutations } from 'vuex'
import zh_CN from 'ant-design-vue/lib/locale-provider/zh_CN'
var qs = require('qs')
export default {
  name: 'vote-basesetting',
  data () {
    return {
      message: '',
      zh_CN,
      file: '',
      // 投票类型
      aWayRadio: ['匿名投票', '实名投票'],
      // 限制传播
      aVoteLimit: ['限制', '不限制'],
      // 投票设置
      sVoteTitle: '',
      sVoteDescript: '',
      aChoiceRadio: ['文字选项', '图片选项'],
      nCurrentImgChoice: 0,
      oVoteChoice: {
        nOption: 0,
        aVoteImgChoice: [
          { imgurl: '', descript: '', votenum: 0 },
          { imgurl: '', descript: '', votenum: 0 },
          { imgurl: '', descript: '', votenum: 0 }
        ],
        aVoteTextChoice: [{ name: 1, value: '', votenum: 0 }, { name: 2, value: '', votenum: 0 }, { name: 3, value: '', votenum: 0 }]
      },
      // 分享设置
      sPushName: '',
      oVoteShowImg: [],
      sPushContent: ''
    }
  },
  watch: {
    // 保存
    bSave (n, o) {
      if (n === true) {
        console.log('保存当前所有数据')
        console.log('点击完成后清空缓存')
      }
      this.setSave(false)
    },
    // 跳转下一步之前监听当前页数必填项是否已输入
    // 当前只能判断页面依次跳转
    nCurrentPage (np, op) {
      if (np > op) {
        if (op === 0) {
          if (this.isNull(this.sVoteType) || this.isNull(this.sVoteTime)) {
            this.setCurrentPage(0)
            this.$message.warning('必填项不能为空')
          }
        }
        if (op === 1) {
          if (this.isNull(this.sVoteTitle)) {
            this.setCurrentPage(1)
            this.$message.warning('必填项不能为空')
            return
          }
          if (this.oVoteChoice.nOption === 0) {
            if (this.isNull(this.oVoteChoice.aVoteTextChoice[0].value) || this.isNull(this.oVoteChoice.aVoteTextChoice[1].value)) {
              this.setCurrentPage(1)
              this.$message.warning('必填项不能为空')
            }
          } else {
            if ((this.isNull(this.oVoteChoice.aVoteImgChoice[0].imgurl) && this.isNull(this.oVoteChoice.aVoteImgChoice[0].descript)) ||
                (this.isNull(this.oVoteChoice.aVoteImgChoice[1].imgurl) && this.isNull(this.oVoteChoice.aVoteImgChoice[1].descript))) {
              this.setCurrentPage(1)
              this.$message.warning('必填项不能为空')
            }
          }
        }
        if (op === 2) {
          if (this.isNull(this.sPushName)) {
            this.setCurrentPage(2)
            this.$message.warning('必填项不能为空')
          }
        }
      } else {
      }
    },
    // 监听输入并存入全局
    sVoteTitle () {
      this.setVoteTitle(this.sVoteTitle)
    },
    sVoteDescript () {
      this.setVoteDescript(this.sVoteDescript)
    },
    oVoteChoice: {
      handler (newN, oldN) {
        this.setVoteChoice(this.oVoteChoice)
        // console.log(this.oVoteChoice)
        // console.log(this.oVoteChoice.nOption, this.oVoteChoice.aVoteTextChoice.length, this.oVoteChoice.aVoteImgChoice.length)
      },
      deep: true
    },
    sPushName () {
      this.setVotePushname(this.sPushName)
    },
    sPushContent () {
      this.setVotePushcontent(this.sPushContent)
    }
  },
  computed: {
    ...mapGetters(['bSave', 'nCurrentPage', 'sVoteType', 'sVoteTime', 'sVoteWay', 'sVoteLimit'])
  },
  methods: {
    // 判断字符串是否为空或空格
    isNull (str) {
      if (str === '') return true
      var regu = '^[ ]+$'
      var re = new RegExp(regu)
      return re.test(str)
    },
    // 类型
    fSelectOnChange (value) {
      if (value === '单选') {
        this.setVoteType('0')
      } else {
        this.setVoteType('1')
      }
    },
    // 截止日期
    moment,
    range (start, end) {
      const result = []
      for (let i = start; i < end; i++) {
        result.push(i)
      }
      return result
    },
    fDateOnChange (date, dateString) {
      this.setVoteTime(dateString)
      console.log(date, dateString)
    },
    // 匿名投票
    fRadioOnChange (e) {
      if (e.target.value === '匿名') {
        this.setVoteWay(0)
      } else {
        this.setVoteWay(1)
      }
    },
    // 限制传播
    fLimitOnChange (e) {
      if (e.target.value === '限制') {
        this.setVoteLimit(0)
      } else {
        this.setVoteLimit(1)
      }
    },
    // 文字图片选项
    fChoiceRadioOnChange (e) {
      if (e.target.value === '文字选项') {
        this.oVoteChoice.nOption = 0
        // console.log(this.oVoteChoice.nOption)
      } else {
        this.oVoteChoice.nOption = 1
        // console.log(this.oVoteChoice.nOption)
      }
    },
    // 图片改变
    fCurrentImgChoice (key) {
      this.nCurrentImgChoice = key
    },
    // 减去选项
    fChoiceReduceClick (val) {
      if (this.oVoteChoice.nOption === 0) {
        this.oVoteChoice.aVoteTextChoice.splice(val, 1)
      } else {
        this.oVoteChoice.aVoteImgChoice.splice(val, 1)
      }
    },
    // 鼠标移动删减图标变红
    fChoiceMouseover ($event) {
      $event.currentTarget.childNodes[0].style.color = 'red'
    },
    fChoiceMouseout (e) {
      e.currentTarget.childNodes[0].style.color = 'grey'
    },
    // 新增选项
    // v-for循环渲染列表,class取前两项设置样式
    // 点击下一步时判断前两项是否为空
    fChoiceAddClick () {
      if (this.oVoteChoice.nOption === 0) {
        let name = this.oVoteChoice.aVoteTextChoice.length + 1
        var oNewTextChoice = { name: name, value: '', votenum: 0 }
        this.oVoteChoice.aVoteTextChoice.push(oNewTextChoice)
      } else {
        var oNewImgChoice = { imgurl: '', descript: '', votenum: 0 }
        this.oVoteChoice.aVoteImgChoice.push(oNewImgChoice)
      }
    },
    // 推送封面
    fPushImg (file) {
      var beforepush = new Promise((resolve, reject) => {
        this.file = file
        // console.log('1')
        return false
      })
      beforepush.then(this.fVotePushImgChange())
    },
    fVotePushImgChange () {
      console.log('enter')
      // console.log(2)
      var formData = new FormData()
      formData.append('file', this.file)
      if (this.nCurrentImgChoice === 1000) {
        this.$axios.post('/upload', formData).then((result) => {
          this.setVotePushImgurl('http://10.29.15.38:3008/vote/acquire?path=./' + result.data.Url)
          // this.setVotePushImgurl('http://10.21.8.95:12306/getPic?path=' + result.data)
        })
      } else {
        this.$axios.post('/upload', formData).then((result) => {
          this.oVoteChoice.aVoteImgChoice[this.nCurrentImgChoice].imgurl = 'http://10.29.15.38:3008/vote/acquire?path=./' + result.data.Url
          // console.log(this.oVoteChoice.aVoteImgChoice[this.nCurrentImgChoice].imgurl)
        })
      }
    },
    // 数组对象属性名转换
    convertKey (arr, key) {
      let newArr = []
      arr.forEach((item, index) => {
        let newObj = {}
        for (var i = 0; i < key.length; i++) {
          newObj[key[i]] = item[Object.keys(item)[i]]
        }
        newArr.push(newObj)
      })
      console.log(newArr)
      return newArr
    },
    // 按钮
    fPrestepClick () {
      let s = this.nCurrentPage
      s--
      this.setCurrentPage(s)
    },
    fNextstepClick () {
      let s = this.nCurrentPage
      s++
      this.setCurrentPage(s)
    },
    fSavestepClick () {
      this.setCurrentPage(3)
      let params = {
        'type': this.sVoteType,
        'deadline': this.sVoteTime,
        'anonymity': this.sVoteWay,
        'title': this.sVoteTitle,
        'detail': this.sVoteDescript,
        'optionsType': this.oVoteChoice.nOption,
        'isLimit': this.sVoteLimit,
        'options': '',
        'totalNum': 0
      }
      console.log(params)
      if (this.oVoteChoice.nOption === 0) {
        params.options = JSON.stringify(this.oVoteChoice.aVoteTextChoice)
      } else {
        params.options = JSON.stringify(this.convertKey(this.oVoteChoice.aVoteImgChoice, ['name', 'value', 'votenum']))
      }
      // console.log(params)
      this.$axios.post('/save', qs.stringify(params))
        .then((result) => {
          this.message = result
          console.log(result)
          console.log(1)
          console.log(this.message)
        }).catch((res) => {
          console.log(res)
        })
    },
    ...mapMutations({
      setSave: 'setSave',
      setCurrentPage: 'setCurrentPage',
      setVoteType: 'setVoteType',
      setVoteTime: 'setVoteTime',
      setVoteWay: 'setVoteWay',
      setVoteLimit: 'setVoteLimit',
      setVoteTitle: 'setVoteTitle',
      setVoteDescript: 'setVoteDescript',
      setVoteChoice: 'setVoteChoice',
      setVotePushname: 'setVotePushname',
      setVotePushImgurl: 'setVotePushImgurl',
      setVotePushcontent: 'setVotePushcontent'
    })
  }
}
</script>

<style>
/**********************组件步骤条样式暂覆盖不了,important覆盖**************************/
/* 步骤条 */
.ant-steps-item{text-align: left; margin: 0;}
.ant-steps-item:nth-child(-n+2){ padding-right: 27px;}
.ant-steps-item-tail{ margin: 0 51px !important;}
/* :global{
  .ant-steps-item-tail{ margin: 0 51px;}
} */
/* 表单长度 */
.ant-input{
  width: 234px;
}
.ant-upload-select-text .ant-btn-default{
  width: 235px;
}
.ant-selects {
  width: 234px;
}
.ant-calendar-picker {
  width: 234px;
}
.ant-radio-wrapper{
  width: 104px;
}
/* 上传图片不显示预览 */
.ant-upload-list-text{
  display: none;
}
</style>

<style scoped>
/* 右侧流程 */
.select-content {
  height: 733px;
  position: relative;
}
.steps-content{
  padding: 18px 25px 0 32px;
}
.ant-row-title {
  width: 70px;
  font-size: 14px; line-height: 22px;
  text-align: right;
  white-space: nowrap;
}
.ant-row-title-center{
  text-align: center; color: #000;
}
.ant-row {
  text-align: left;
}
/* 必须项添加星标 */
.title-must:before {
  content: "*";
  color: red;
  font-size: 16px;
}
/* 步骤条 */
.select-content-steps{
  width: 355px; height: 66px;
  margin: 32px auto 0 auto;
}
/* 投票类型 */
.vote-type .ant-row {
  margin-top: 16px;
}
.vote-type-vote {
  text-align: left;
}
/* 投票设置 */
.vote-setting-base .ant-row {
  margin-top: 16px;
}
.vote-setting-base{
  margin-bottom: 8px;
}
.vote-setting-choice {
  height: 380px;
  overflow: scroll;
}
.vote-setting-choice::-webkit-scrollbar {
  display: none;
}
.vote-setting-choice .ant-row {
  margin-top: 16px;
}
.vote-setting-choicewhich:nth-child(-n+2) .ant-row-title:before {
  content: "*";
  color: red;
  font-size: 16px;
  position: relative;
}
.ant-row-add {
  width: 308px; margin: 8px 0 0 11px;
  border-style: dotted;
  color: grey;
}
.pop-choice{
  height: 30px; line-height: 30px; cursor: pointer;
}
/* 分享设置 */
.vote-sharingsetting .ant-row {
  margin-top: 16px;
}
/* 完成设置 */
.complete-img{
  width: 72px; height: 72px;
  margin-top: 75px;
}
.complete-p{
  margin-top: 24px;
  font-size: 24px; font-weight: bold;
  color: rgba(0,0,0,0.85);
  line-height: 32px;
}
/* 底部按钮 */
.content-bt {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 46px;
  line-height: 46px;
  margin: 0;
  text-align: center;
  box-shadow: inset 0 1px 0 0 rgba(0,0,0,0.09);
}
.ant-bt {
  width: 49%;
  border: none;
  font-size: 16px; color: #000;
  cursor: pointer;
}
.anticon {
  font-size: 20px;
  color: grey;
}
</style>



