<template>
  <div>
    <div class="center-show" :style="bgImg">
      <div class="icon-show">
        <!-- <img :src="toSon.imgList.logourl"> -->
        <img src="../assets/scancode/logo.png">
      </div>
      <div class="main-heading" :style="mainHeadingBg">
        <!-- <h1 style="color:red">主标题主标题</h1> -->
        <!-- <h1 v-show="toSon.mainHeadingInputShow" style="margin-top:20px">{{toSon.shareHeading}}</h1> -->
        <!-- <img :src="toSon.imgList.headingurl" v-show="toSon.mainHeadingUploadShow"> -->
        <h1>天翼云</h1>
      </div>
      <div class="sub-heading" :style="subHeadingBg">
        <h3>副标题</h3>
        <!-- <h2 v-show="toSon.subHeadingInputShow">{{toSon.subHeading}}</h2> -->
        <!-- <img :src="toSon.imgList.subheadingurl" v-show="toSon.subHeadingUploadShow"> -->
      </div>
      <div class="scancode-show" :style="scanCodeBg">
       

      

        <!-- <video src>视频展示</video> -->
      </div>
      <div class="subtext" :style="subTextBg" >
      
           <h3>文本文字文本文字</h3>
         
      </div>
      <div class="miantext" :style="mainTextBg">
        <h2>文本文字文本文字</h2>
         

      </div>
      
    </div>
  </div>
</template>
<script>
export default {
  name: "scancodetemplate",
  data() {
    return {
      bgImg: {
        backgroundImage: "url(" + require("../assets/scancode/bg_1.png") + ")"
      },
      mainHeadingBg: {
        backgroundImage:
          "url(" + require("../assets/scancode/maintitlebg.png") + ")"
      },
      subHeadingBg: {
        backgroundImage:
          "url(" + require("../assets/scancode/textbg_1.png") + ")"
      },
      scanCodeBg:{
        backgroundImage:
          "url(" + require("../assets/scancode/scancode.png") + ")"
      },
      subTextBg:{
          backgroundImage:
         "url(" + require("../assets/scancode/textbg_1.png") + ")"

      },
      mainTextBg:{
         backgroundImage:
         "url(" + require("../assets/scancode/textbg_2.png") + ")"

      }
    };
  }
};
</script>
<style scoped>
.center-show {
  text-align: center;
  margin: 0 auto;
  width: 375px;
  height: 603px;
  position: relative;
  box-shadow: 1px 1px 5px gray;
}
.icon-show {
  width: 42.7px;
  height: 30px;
  display: inline-block;
  margin-left: -290px;
  margin-top: 20px;
}
.icon-show img {
  width: 50px;
  height: 35px;
}
.main-heading {
  text-align: center;
  /* padding-top:82px; */
  margin-left: 50px;
  margin-top: 50px;
  width: 275px;
  height: 68px;
  white-space: normal;
  word-break: break-all;
  word-wrap: break-word;
  overflow: hidden;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.main-heading img {
  width: 75%;
  height: 60px;
}
.sub-heading {
  margin-left: 110px;
 
  width: 160px;
  height: 26px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.scancode-show {
  position: relative;
  
  margin-left: 70px;
  margin-top: 10px;
  width: 240px;
  height: 240px;

 
  background-size: 100% 100%;
}
.subtext{

  height:32px ;
  width: 190px;
  border: 1px solid royalblue;
  margin-left: 95px;
  margin-top: 20px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
 

}
h3{
  color: white;
}

.miantext{
  width: 240px;
  height: 32px;
  margin-left: 70px;
  margin-top: 20px;
  border: 1px solid rebeccapurple;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.miantext  h2{
   color: white;
}
h1{
 margin-top: 10px;

}

@font-face {
  font-family: happy;
  src: url("../assets/text.ttf");
}
</style>


