<template>
  <div>
    <p class="header">
      <span class="header-siderleft">
        <span class="header-link" @click="fLinktoCenterappClick">返回上一级</span>
        <span>|</span>
        <span class="header-link">翼企应用</span>
        <span>></span>
        <span>产品框架</span>
      </span>
      <span class="header-siderright">
        <a-button class="head-bt" type="primary" size="small" @click="fClickToSave" ghost>保存</a-button>
        <a-button class="head-bt" type="primary" size="small" @click="fPreviewVote" ghost>预览</a-button>
        <a-button
          class="head-bt"
          style="background-color: #f5222d; color: #f5f5f5;"
          type="danger"
          size="small"
          @click="sendData"
        >发布</a-button>
      </span>
    </p>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
var qs = require('qs')
export default {
  name: 'content-header',
  data () {
    return {
      Id: 0
    }
  },
  methods: {
    fLinktoCenterappClick () {
      this.$router.push({ name: 'CenterApp' })
    },
    // 预览
    fPreviewVote () {
      this.setPreview(true)
    },
    // 保存
    fClickToSave () {
      this.setSave(true)
    },
    sendData: function () {
      this.Id++
      // console.log("点击发送")
      let videoData = this.$store.getters.userData
      //  console.log(videoData)
      let jsonData = {
        logo: videoData.imgList.logourl,
        Title: {
          word: videoData.mainHeading,
          Pic: videoData.imgList.headingurl,
          bacPic: videoData.imgList.headingbg

        },
        Video: videoData.imgList.video,
        Text: videoData.textarea,

        Other: {
          bacpic: videoData.imgList.bgurl,
          bacColor: videoData.imgList.bgcolor

        },
        VideoId: this.Id
      }
      this.$axios.post('http://10.21.8.95:12306/api/upLoadData', qs.stringify(jsonData),
        {headers: { 'Content-Type': 'application/x-www-form-urlencoded' }})
        .then(res => {
          console.log(res)
        })
      console.log(jsonData)
      console.log(qs.stringify(jsonData))
    },
    ...mapMutations({setPreview: 'setPreview', setSave: 'setSave'})
  },
  computed: {
    ...mapGetters(['bPreview'])
  }
}
</script>

<style scoped>
.header {
  width: 1440px;
  height: 40px;
  padding: 0 18px 0 21px;
  margin: 0;
  text-align: left;
  line-height: 40px;
  background-color: #fefefe;
}
.header-link {
  cursor: pointer;
  color: #1890ff;
}
.header-siderleft {
  font-size: 12px;
}
.header-siderright {
  float: right;
}
.head-bt {
  margin-left: 16px;
  width: 48px;
  height: 24px;
  border-radius: 8px;
}
</style>



