<template>
<div class="centerapp" @click="bShowMenu = false">
  <p class="centerapp-head">
    <a-checkbox @click="fAllCheck" class="app-check">
    </a-checkbox>
    <span>全选</span>
    <span class="app-type">
      <span>全部分类</span>
      <span>
        <a-icon class="app-typeicon" type="bars"></a-icon>
      </span>
    </span>
  </p>
  <div class="applist">
    <!--应用列表-->
    <single-app
    v-for="(item,index) in aAppdata" :key="index + 1" :sApp="item"
    @contextmenu.native="fShowMenu(item, $event)"
    :bAllCheck=bAllCheck>
    </single-app>
    <!--添加应用-->
    <div @click="fAppliPushRouterClick($event)" class="appli appli-add">
      <div class="appli-left">
        <img class="appli-left-img" :src=sAppAdd.url href="app">
      </div>
      <div class="appli-right tovertical">
        <p class="appli-right-p appli-right-head">{{sAppAdd.name}}</p>
        <p class="appli-right-p appli-right-content">{{sAppAdd.descript}}</p>
      </div>
    </div>
  </div>
    <!--右键菜单-->
  <ul class="contextmenu" ref="contextmenu" v-show = bShowMenu>
    <!--li class="contextmenu-li" @click="fContextmenuClick($event)">添加</li-->
    <li class="contextmenu-li" @click="fContextmenuClick($event)">删除</li>
    <li class="contextmenu-li" @click="fContextmenuClick($event)">查看详情</li>
  </ul>
</div>
</template>

<script>
import SingleApp from '../base/centerapp/single-app'

import vote from '../assets/app/vote.png'
import micromv from '../assets/app/micromv.png'
import scanpic from '../assets/app/scanpic.png'
import email from '../assets/app/email.png'
import add from '../assets/app/add.jpg'
export default {
  name: 'CenterApp',
  data () {
    return {
      bAllCheck: false,
      bShowMenu: false,
      // 右键当前app
      oWhichApp: {},
      aAppdata: [
        {
          url: vote,
          name: '投票活动',
          descript: '活动简介说明说明说明',
          state: '已添加'
        }, {
          url: micromv,
          name: '微视频',
          descript: '活动简介说明说明说明',
          state: '已添加'
        }, {
          url: email,
          name: '邮件信',
          descript: '活动简介说明说明说明',
          state: '已添加'
        }, {
          url: scanpic,
          name: '扫码页',
          descript: '活动简介说明说明说明',
          state: '已添加'
        }
      ],
      sAppAdd: {
        url: add,
        name: '应用商城',
        descript: '点击获取更多应用'
      }
    }
  },
  methods: {
    // 全选
    fAllCheck () {
      this.bAllCheck = !this.bAllCheck
    },
    // 应用商城
    fAppliPushRouterClick ($event) {
      this.$router.push({name: 'AppStore'})
    },
    // 右键获取当前app,菜单显示及定位
    fShowMenu (item, parameter) {
      this.oWhichApp = item
      parameter.preventDefault()
      // console.log(parameter.currentTarget.querySelector('.appli-right-head'))
      this.bShowMenu = false
      this.bShowMenu = true
      var x = parameter.clientX
      var y = parameter.clientY
      this.$refs.contextmenu.style.position = 'absolute'
      this.$refs.contextmenu.style.top = parseInt(y) + 'px'
      this.$refs.contextmenu.style.left = parseInt(x) + 'px'
      // this.entityTreeContextMenu.axios = {
      // x, ys
      // }
    },
    // 菜单点击事件
    fContextmenuClick ($event) {
      let sWhichOprate = $event.currentTarget.innerText
      if (sWhichOprate === '删除') {
        console.log(sWhichOprate + ':' + this.oWhichApp.name)
      } else if (sWhichOprate === '查看详情') {
        this.$router.push({name: 'AppDetail', params: {oAppData: this.oWhichApp}})
      } else {}
      this.bShowMenu = false
    }
  },
  components: {'single-app': SingleApp}
}
</script>

<style scoped>
.centerapp{
  width: 1160px; margin: auto;
}
/*头部*/
.centerapp-head{
  height: 54px; line-height: 54px; margin: 0; padding: 0 25px 0 29px;
  text-align: left; font-size: 14px; color: rgba(0,0,0,0.65); font-weight: bold;
  background-color: #fafafa;
  border-bottom: 1px solid #e8e8e8;
}
.app-check{ width: 16px; height: 18px;}
.app-type{ float: right;}
.app-typeicon{ font-size: 16px; margin-left: 8px;}
/*列表*/
.applist{
  display: flex; flex-wrap: wrap;
  margin: 0 40px 0 60px;
}
.appli-add{
  background: #FAFAFA;
  border: 3px dotted #D9D9D9;
  border-radius: 2px;
}
/* 右键菜单 */
.contextmenu{
  z-index: 5;
  width: 76px; height: 63px; padding: 0;
  background-color: #fff; border: 1px solid rgba(0,0,0,0.15);
  font-size:14px; text-align: center; color: rgba(0,0,0,0.65); list-style: none;
}
.contextmenu-li{
  height: 32px; line-height: 32px;
  cursor: pointer;
}
.contextmenu-li:hover{ background-color: #E6F7FF;}
</style>



