import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  // modules: {
  //   qaService
  // },
  state: {
    bSave: false,
    bPreview: false,
    // currentpage
    nCurrentPage: 0,
    // votetype
    sVoteType: 1,
    sVoteTime: '',
    sVoteWay: 0,
    sVoteLimit: 0,
    // votesetting
    sVoteTitle: '',
    sVoteDescript: '详细说明详细说明详细说明详细说明详细说明详细说明详细说明',
    oVoteChoice: {
      nOption: 0,
      aVoteImgChoice: [
        {name: 1, imgurl: '', descript: ''},
        {name: 2, imgurl: '', descript: ''},
        {name: 3, imgurl: '', descript: ''}
      ],
      aVoteTextChoice: [
        {name: 1, value: ''},
        {name: 2, value: ''},
        {name: 3, value: ''}
      ]
    },
    // sharingsetting
    sVotePushname: '推送标题',
    sVotePushImgurl: '',
    sVotePushcontent: '推送说明',
    userData: []
  },
  getters: {
    bSave (state) { return state.bSave },
    bPreview (state) { return state.bPreview },
    nCurrentPage (state) { return state.nCurrentPage },
    sVoteType (state) { return state.sVoteType },
    sVoteLimit (state) { return state.sVoteLimit },
    sVoteTime (state) { return state.sVoteTime },
    sVoteWay (state) { return state.sVoteWay },
    sVoteTitle (state) { return state.sVoteTitle },
    sVoteDescript (state) { return state.sVoteDescript },
    oVoteChoice (state) { return state.oVoteChoice },
    sVotePushname (state) { return state.sVotePushname },
    sVotePushImgurl (state) { return state.sVotePushImgurl },
    sVotePushcontent (state) { return state.sVotePushcontent },
    userData (state) { return state.userData }
  },
  mutations: {
    setSave (state, bSave) { state.bSave = bSave },
    setPreview (state, bPreview) { state.bPreview = bPreview },
    setCurrentPage (state, nCurrentPage) { state.nCurrentPage = nCurrentPage },
    setVoteType (state, sVoteType) { state.sVoteType = sVoteType },
    setVoteLimit (state, sVoteLimit) { state.sVoteLimit = sVoteLimit },
    setVoteTime (state, sVoteTime) { state.sVoteTime = sVoteTime },
    setVoteWay (state, sVoteWay) { state.sVoteWay = sVoteWay },
    setVoteTitle (state, sVoteTitle) { state.sVoteTitle = sVoteTitle },
    setVoteDescript (state, sVoteDescript) { state.sVoteDescript = sVoteDescript },
    setVoteChoice (state, oVoteChoice) { state.oVoteChoice = oVoteChoice },
    setVotePushname (state, sVotePushname) { state.sVotePushname = sVotePushname },
    setVotePushImgurl (state, sVotePushImgurl) { state.sVotePushImgurl = sVotePushImgurl },
    setVotePushcontent (state, sVotePushcontent) { state.sVotePushcontent = sVotePushcontent },
    setuserData (state, userData) { state.userData = userData }

  },
  actions: {}
})



