<template>
  <div class="voteedit">
    <!--头部-->
    <content-header></content-header>
    <!--内容-->
    <a-layout class="voteedit-content">
      <!--左侧-->
      <a-layout-sider style="min-width: 220px;max-width: 220px;" class="voteedit-contentleft">
        <div class="voteedit-lookshow">
          <div class="voteedit-look" :class="{'voteedit-look-current': nCurrentPage == 0}">
            <span v-show="nCurrentPage !== 0" class="voteedit-look-index">1</span>
            <div class="voteedit-look-content">
              <vote-basesetting-votetype class="vote-content-boxshadow sublook-content"></vote-basesetting-votetype>
            </div>
          </div>
          <div class="voteedit-look" :class="{'voteedit-look-current': nCurrentPage == 1}">
            <div v-show="nCurrentPage !== 1" class="voteedit-look-index"><p>2</p></div>
            <div class="voteedit-look-content">
              <vote-basesetting-votesetting class="vote-content-boxshadow"></vote-basesetting-votesetting>
            </div>
          </div>
          <div class="voteedit-look" :class="{'voteedit-look-current': nCurrentPage == 2}">
            <div v-show="nCurrentPage !== 2" class="voteedit-look-index"><p>3</p></div>
              <div class="voteedit-look-content">
                <vote-basesetting-pushsetting class="vote-content-boxshadow"></vote-basesetting-pushsetting>
              </div>
          </div>
          <div class="voteedit-look" :class="{'voteedit-look-current': nCurrentPage == 3}">
            <div v-show="nCurrentPage !== 3" class="voteedit-look-index"><p>4</p></div>
              <div class="voteedit-look-content">
                <vote-basesetting-complete class="vote-content-boxshadow"></vote-basesetting-complete>
              </div>
          </div>
        </div>
        <!--a-button @click="fNewModelClick" class="voteedit-lookadd">
          <span>新增页面</span>
          <a-icon class="voteedit-lookadd-icon" type="plus"></a-icon>
        </a-button-->
      </a-layout-sider>
      <!--中间-->
      <a-layout-content style="min-width: 810px;">
        <div class="vote-content">
          <div class="vote-content-head">
            <a-icon class="vote-content-headc" type="question-circle"></a-icon>
            <p class="vote-content-headp">活动模式:用户进行投票，最终统计票数及比例你可以设置投票问题和投票选项以及推送设置</p>
          </div>
          <vote-basesetting-votetype class="vote-content-boxshadow" v-show="nCurrentPage == 0"></vote-basesetting-votetype>
          <vote-basesetting-votesetting class="vote-content-boxshadow" v-show="nCurrentPage == 1"></vote-basesetting-votesetting>
          <vote-basesetting-pushsetting class="vote-content-boxshadow" v-show="nCurrentPage == 2"></vote-basesetting-pushsetting>
          <vote-basesetting-complete class="vote-content-boxshadow" v-show="nCurrentPage == 3"></vote-basesetting-complete>
        </div>
      </a-layout-content>
      <!--右侧-->
      <a-layout-sider style="min-width: 396px;max-width: 396px;" class="content-siderright">
        <a-row class="content-right-h">
          <router-link :to="{name: 'VoteBasesetting'}">
            <p :span="8" class="content-select">
              <span class="content-p content-right-h-p">基本设置</span>
              <span><a-icon type="setting"></a-icon></span>
            </p>
          </router-link>
          <div>
            <p :span="8" class="content-select">
              <span class="content-p content-right-h-p">高级设置</span>
              <span><a-icon type="tool"></a-icon></span>
            </p>
          </div>
          <div>
            <p :span="8" class="content-select">
              <span class="content-p content-right-h-p">咨询帮助</span>
              <span><a-icon type="message"></a-icon></span>
            </p>
          </div>
        </a-row>
        <router-view></router-view>
      </a-layout-sider>
    </a-layout>
    <!--预览-->
    <div class="vote-preview"  style="position: fixed;" v-show="this.bPreview == true"
        @mousewheel.prevent="" @mousemove="fSubPageChange">
      <div class="preview-detail" @mousewheel.stop="">
        <a-icon theme="filled" class="preview-left preview-operate" type="left-circle"
                v-show="nCurrentPage !== 0" @click.stop="fClickToPage('left')">
        </a-icon>
        <img class="preview-bg" src="../assets/vote/preview.png">
        <vote-basesetting-votetype v-show="nCurrentPage == 0" class="preview-content"></vote-basesetting-votetype>
        <vote-basesetting-votesetting v-show="nCurrentPage == 1" class="preview-content"></vote-basesetting-votesetting>
        <vote-basesetting-pushsetting v-show="nCurrentPage == 2" class="preview-content"></vote-basesetting-pushsetting>
        <vote-basesetting-complete v-show="nCurrentPage == 3" class="preview-content"></vote-basesetting-complete>
        <img class="preview-close" @click="fPreviewVote" src="../assets/vote/previewclose.png">
        <a-icon theme="filled" class="preview-right preview-operate" type="right-circle"
                v-show="nCurrentPage !== 3" @click.stop="fClickToPage('right')">
        </a-icon>
      </div>
    </div>
  </div>
</template>

<style scoped>
.voteedit{ width: 1440px; margin: auto;}
.voteedit-content{margin-top: 6px; background-color: #E3E6EA;}
.ant-layout-sider{
  background-color: #fefefe;
  overflow: scroll;
  position: relative;
}
.ant-layout-sider::-webkit-scrollbar{ display: none;}
/* 侧边预览 */
.voteedit-contentleft{
  margin-top: 6px;
}
.voteedit-look-current{
  background-color: #eaf1ff;
}
.voteedit-look{
  height: 192px; padding: 18px 0;
  position: relative;
}
.voteedit-look-index{
  position: absolute; left: 30px; top: 76px;
  font-size: 12px; color: rgba(0,0,0,0.60);
}
.voteedit-look-content{
  width: 390px; padding: 2px;
  transform-origin: 65px 0;
  transform: scale(0.231);
  -webkit-transform-origin: 82px 0;
  -webkit-transform: scale(0.231);
  background-color: #eee;
  text-align: left;
}
/* .voteedit-lookadd{
  position: absolute; bottom: 0; left: 0;
  z-index: 5;
  width: 100%;
} */
.anticon{ font-size: 14px; color: grey;}
/* 中间内容 */
.vote-content-boxshadow{
  box-shadow: 2px 0 2px #ddd, -2px 0 2px #ddd, 0 2px 2px #ccc;
}
.vote-content{
  width: 390px; margin: auto;
}
.vote-content-head{
  display: table;
  margin: 26px auto; width: 280px;
  font-size: 12px;
  color: #989898;
}
.vote-content-headc{
  display: table-cell; vertical-align: middle;
  width: 14px;
}
.vote-content-headp{
  display: table-cell;
  width: 252px; margin: 0; padding-left: 11px;
  text-align: left;
}
/* 右侧 */
.content-siderright{
  margin-top:6px; border-radius: 5px;
}
/* 右侧设置 */
.content-right-h{
  padding-top: 9px; display: flex; margin-bottom: 2px;
  box-shadow: inset 0 -1px 0 0 rgba(0,0,0,0.09);
}
.content-select{
  display: inline-block;
  width: 132px; height: 46px; margin: 0;
  cursor: pointer; border: none;
  font-size: 16px; line-height: 46px; color: rgba(0,0,0,0.25);
}
.content-right-h-p{
  display: inline-block; width: 75px;
}
.router-link-active .content-select{
  color: #1890ff;
  border-bottom: 2px solid #1890ff;
}
.anticon{ font-size: 20px;}
.router-link-active .anticon{ color: #1890ff;}
/* 预览 */
.vote-preview{
  top: 0; left: 0;
  z-index: 10; width: 100%; height: 100%;
  text-align: center; background-color: rgba(128, 128, 128, 0.2);
  position: relative;
}
.preview-detail{
  margin: 45px auto; width: 420px; height: 719px;
  position: relative;
}
.preview-bg{
  position: absolute;
  top: 0; left: -4px;
}
.preview-content{
  width: 180px; height: 333px;
  position: absolute;
  bottom: 25px; right: 20px;
}
.preview-close{
  width: 40px;
  position: absolute; right: -40px; top: 70px;
  cursor: pointer;
}
.preview-detail .anticon{
  font-size: 50px;
  cursor: pointer;
}
.preview-left{
  position: absolute;
  top: 300px; left: -180px;
}
.preview-right{
  position: absolute;
  top: 300px; right: -180px;
}
</style>

<script>
import ContentHeader from '../base/content-header'
import VoteBasesettingVotetype from '../base/generatevote/vote-basesetting-votetype'
import VoteBasesettingVotesetting from '../base/generatevote/vote-basesetting-votesetting'
import VoteBasesettingPushsetting from '../base/generatevote/vote-basesetting-pushsetting'
import VoteBasesettingComplete from '../base/generatevote/vote-basesetting-complete'
import {mapGetters, mapMutations} from 'vuex'
export default {
  name: 'GenarateVote',
  data () {
    return {
      // 控制预览左右
      // direction: ''
    }
  },
  methods: {
    // 左右键进入相应页面
    fClickToPage (value) {
      if (value === 'left') {
        let s = this.nCurrentPage
        if (s < 1) s = 0
        else s--
        this.setCurrentPage(s)
      } else {
        let s = this.nCurrentPage
        if (s > 2) s = 3
        else s++
        this.setCurrentPage(s)
      }
    },
    // 是否预览
    fPreviewVote () {
      this.setPreview(false)
    },
    // 需求：预览长页滚动,鼠标左右控制预览图当前页数
    fSubPageChange (e) {
      // this.direction = e.movementX > 0 ? 'right' : 'left'
    },
    // 父容器(遮罩层)prevent，内容滚动,防止冒泡
    /* ????????????????????????预览到底或顶时父容器跟着滚动?????????????????????????????????? */
    // fSubPageScroll (e) {
    // 需求：滚轮改变页数
    // e.preventDefault()
    // var direction = e.deltaY > 0 ? 'down' : 'up'
    // if (direction === 'down') {
    //   let s = this.nCurrentPage
    //   if (s > 2) s = 3
    //   else s++
    //   this.setCurrentPage(s)
    // } else {
    //   let s = this.nCurrentPage
    //   if (s < 1) s = 0
    //   else s--
    //   this.setCurrentPage(s)
    // }
    // },
    ...mapMutations({setPreview: 'setPreview', setCurrentPage: 'setCurrentPage'})
  },
  computed: {
    ...mapGetters(['nCurrentPage', 'bPreview'])
  },
  components: {ContentHeader, VoteBasesettingVotetype, VoteBasesettingVotesetting, VoteBasesettingPushsetting, VoteBasesettingComplete}
}
</script>



