<template>
<div @click="fAppliPushRouterClick($event)" class="appli" :class="{'applimouseover': bMouseover}"
      @mouseover="fAppliClasschangeMouseover($event)"
      @mouseout="fAppliClasschangeMouseout($event)">
  <a-checkbox ref="checkbox" @click.stop="fAppCheckClick" :checked=bCheck v-show=bShowCheckbox class="appli-check">
  </a-checkbox>
  <div class="appli-left">
    <img class="appli-left-img" :src=sApp.url href="app">
  </div>
  <div class="appli-right tovertical">
    <p class="appli-right-p appli-right-head">{{sApp.name}}</p>
    <p class="appli-right-p appli-right-content">{{sApp.descript}}</p>
    <p class="appli-right-p appli-right-state"
      :class="{'state-active': bState}"
      v-show="sApp.state == '已添加' || !bIsAdd"
      v-on:mouseover="fApplistateChangeButtonMouseover($event)"
      v-on:mouseout="fApplistateChangeButtonMouseout($event)"
      @click.stop="fAppStateClick($event)">
      {{sApp.state}}
    </p>
    <!--添加时加载-->
    <!--------------------加载条透明,加载关闭按钮显示隐藏------------------------>
    <p class="appli-right-p appli-right-load"
        @click.stop=""
        v-if="sApp.state == '添加'">
      <span ref="loadingtransition" class="appli-right-loading" :class="{'loading-transition': bIsAdd}"></span>
      <a-icon v-show="bIsAdd" @click.stop="fCloseAddClick()" class="appli-right-loadclose" type="close-circle"></a-icon>
    </p>
  </div>
</div>
</template>

<script>
export default{
  name: 'single-app',
  props: ['sApp', 'bAllCheck'],
  data () {
    return {
      t: null,
      bShowCheckbox: false,
      bMouseover: false,
      bState: false,
      bCheck: false,
      bIsAdd: false
    }
  },
  created () {
    this.fWatchState()
  },
  watch: {
    bAllCheck () {
      this.fShowCheckbox()
    }
  },
  methods: {
    // 初始化应用状态样式
    fWatchState () {
      if (this.sApp.state === '已添加') this.bState = false
      else if (this.sApp.state === '添加') this.bState = true
      else {}
    },
    // 根据是否全选显示隐藏
    fShowCheckbox () {
      console.log(this.bCheck)
      if (this.bAllCheck === true) {
        this.bShowCheckbox = true
        this.bCheck = true
      } else {
        this.bShowCheckbox = false
        this.bCheck = false
      }
    },
    // 单个组件选择
    fAppCheckClick (e) {
      this.bCheck = !this.bCheck
      console.log(e.currentTarget.checked)
    },
    // 鼠标移进移出背景改变
    fAppliClasschangeMouseover ($event) {
      this.bMouseover = true
    },
    fAppliClasschangeMouseout ($event) {
      this.bMouseover = false
    },
    // 点击组件进入应用
    fAppliPushRouterClick ($event) {
      this.$router.push({name: 'AppDetail', params: {oAppData: this.sApp}})
      // this.$router.push({name: 'WelCome'})
    },
    // 移进移出按钮状态改变
    fApplistateChangeButtonMouseover ($event) {
      var sText = $event.currentTarget.innerText
      if (sText === '已添加') {
        $event.currentTarget.innerText = '打开'
        this.bState = true
      } else if (sText === '添加') {
      } else {}
    },
    fApplistateChangeButtonMouseout ($event) {
      var sText = $event.currentTarget.innerText
      if (sText === '已添加' || sText === '打开') {
        $event.currentTarget.innerText = '已添加'
        this.bState = false
      } else {
      }
    },
    // 点击按钮进入欢迎页
    fAppStateClick ($event) {
      let sWhichState = $event.currentTarget.innerText
      // 打开应用将基本信息传递,详情页根据基本信息进行检索
      if (sWhichState === '打开') {
        if (this.sApp.name === '投票活动') {
          this.$router.push({name: 'GenerateVote'})
        } else if (this.sApp.name === '微视频') {
          this.$router.push({name: 'APPVideo'})
        } else if (this.sApp.name === '扫码页') {
          this.$router.push({name: 'scancode'})
        } else if (this.sApp.name === '邮件信') {
          this.$router.push({name: 'mailletter'})
        } else {
          console.log('跳转欢迎页')
        }
      } else if (sWhichState === '添加') {
        // 点击添加后加载动画
        this.bIsAdd = true
        this.$refs.loadingtransition.style.transition = '2s'
        this.fAddApp()
      }
    },
    // 模拟添加
    fAddApp () {
      this.t = setTimeout(
        () => {
          console.log(this.sApp.state)
          this.sApp.state = '已添加'
          this.fWatchState()
        }, 1000)
    },
    // 点击关闭加载
    fCloseAddClick () {
      clearTimeout(this.t)
      this.bIsAdd = false
      this.$refs.loadingtransition.style.transition = '0s'
      this.sApp.state = '添加'
      this.fWatchState()
    }
  }
}
</script>

<style>
.appli{
  display: flex;
  width: 245px; height: 120px; margin: 20px 20px 0 0;
  background-color: #fafafa;
  position: relative;  cursor: pointer;
}
.applimouseover{
  background-color: #f2f2f2;
}
.appli:nth-child(4n+0){
  margin-right: 0;
}
.tovertical{
  vertical-align: middle;
}
.appli-left{
  margin: 30px 19px 26px 25px;
  width: 64px; height: 64px; text-align: center;
}
.appli-left-img{
  width: 64px; height: 64px; border-radius: 5px; cursor: pointer;
}
.appli-right{
  width: 120px; margin:26px 17px 0 0;
  text-align: left;
}
.appli-right-p{ margin: 0;}
.appli-right-head{
  height: 22px; line-height: 22px;
  font-size: 14px; font-weight: bold; color: rgba(0,0,0,0.85);
}
.appli-right-content{
  height: 17px; line-height: 17px; margin: 4px 0 5px 0;
  white-space: nowrap; text-overflow: ellipsis; overflow: hidden;
  font-size: 12px; color: #888;
}
.appli-right-state{
  text-align: center;
  width: 44px; height: 18px; line-height: 18px;
  border-radius: 4px;
  border: 1px solid #D9D9D9;
  background-color: rgba(0,0,0,0.04);
  white-space: nowrap;
  font-size: 12px; color: rgba(0,0,0,0.25);
}
.state-active{ background: #1890FF; color: #fff;}
/*加载*/
.appli-right-load{
  display: flex; position: relative;
  width: 81px;
}
.appli-right-loading{
  display: inline-block;
  width: 0; height: 0; margin-top: 4px;
  border: 3px solid transparent; border-radius: 8px;
}
.loading-transition{
  width: 50px;
  border: 3px solid  #1890FF;
}
.appli-right-loadclose{
  position: absolute; right: 0;
  color: #1890FF;
}
/*单选*/
.appli-check{
  position: absolute; top: 8px; left: 8px;
  width: 16px; height: 16px;
}
</style>



