<template>
  <div>
    <div class="center-show" :style="bgImg">
      <div class="icon-show">
        <!-- <img :src="toSon.imgList.logourl"> -->
        <img src="../assets/video/Bitmap.png" alt>
      </div>
      <div class="main-heading">
          <h1 style="color:red">主标题主标题</h1>
        <!-- <h1 v-show="toSon.mainHeadingInputShow" style="margin-top:20px">{{toSon.shareHeading}}</h1> -->
        <!-- <img :src="toSon.imgList.headingurl" v-show="toSon.mainHeadingUploadShow"> -->
      </div>
      <div class="sub-heading">
          <h2>副标题副标题</h2>
        <!-- <h2 v-show="toSon.subHeadingInputShow">{{toSon.subHeading}}</h2> -->
        <!-- <img :src="toSon.imgList.subheadingurl" v-show="toSon.subHeadingUploadShow"> -->
      </div>
      <div class="video-show">
          
        <div class="cover-show">
          <!-- <img :src="toSon.imgList.coverurl"> -->
        </div>

        <div class="triangl-show">
          <!-- <img :src="imgList.triangleurl "> -->
          <img src="../assets/video/triangle.png" alt="">
         
        </div>

        <!-- <video src>视频展示</video> -->
      </div>
      <div class="text-show">视频相关消息展示</div>
      <div class="icon-like">
        <!-- <img src="../assets/like.png" alt @click="counter += 1"> -->
        <!-- <p>{{counter}}</p> -->
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "template-second",
  data() {
    return {
      bgImg: {
        backgroundImage: "url(" + require("../assets/video/bg-2.png") + ")",
        backgroundPosition: "center center",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover"
      }
    };
  }
};
</script>
<style scoped>

.center-show {
  text-align: center;
  margin: 0 auto;
  width: 375px;
  height: 663px;
  position: relative;
}
.icon-show {
  width:118.8px;
  height: 30px;
  display: inline-block;

  margin-top: 20px;
  margin-left: -230px;  
 
 
 
}
.icon-show img {
  width:  100%;
  height: 30px;
  
}
.main-heading {

 
  margin-top: 56pX;
  margin-left: 92px;

  
  height: 45px;
  width: 192px;
  white-space: normal;
  word-break: break-all;
  word-wrap: break-word;
  overflow: hidden;
  
}
.main-heading img {
  width: 75%;
  height: 60px;
}



.video-show {
  position: relative;
  margin: 0 auto;
  width: 295px;
  height: 165px;
  
 background: url("../assets/video/video.png")no-repeat;
  background-size:  100% 100%;
}
.triangl-show {
  width: 48px;
  height: 61px;
  position: absolute;
  margin: 0;
  top: 30%;
  left: 45%;
}
.triangl-show img {
  width: 100%;
  height: 100%;
}
.cover-show img {
  width: 300px;
  height: 300px;
}
.text-show {
  text-align: left;
  margin: 24px auto;
  width: 80%;
  height: 100px;
  white-space: normal;
  word-break: break-all;
  word-wrap: break-word;
  overflow: hidden;

}
.icon-like {
  position: absolute;
  right: 35px;
  bottom: 15px;
}
@font-face {
  font-family: happy;
  src: url("../assets/text.ttf");
}
</style>


